@import url('https://fonts.googleapis.com/css2?family=Gelasio:wght@400;500;700&family=Noto+Sans+KR:wght@300;400;500;700&family=Open+Sans:ital,wght@0,400;0,500;0,700;1,300&display=swap');

* {
 font-family: 'Noto Sans KR', sans-serif;
}

h2 {
 text-align: center;
 padding: 50px 0px;
}

ul, li, ol {
 list-style: none;
 margin: 0;
 padding: 0;
}

ul {
 display: flex;
 max-width: 1200px;
 margin: 0px auto;
}

ul li {
 width: 23%;
 padding: 0 1%;
 overflow: hidden;
}

ul li p {
 width: 100%;
 overflow: hidden;
}

ul li p:first-child {
 position: relative;
}

ul li p:first-child img {
 object-fit: cover;
 width: 100%;
 height: 100%;
 transition: 0.7s;
}

ul li p:first-child img:hover {
 transform: scale(1.1);
}

.delivery {
 color: #999;
 font-size: 13px;
 padding: 5px 0px;
}

p {
 padding: 0;
 margin: 0px 0px;
}

.desc {
 padding: 4px 0px;
 font-size: 11px;
 color: #999;
}
.price {
 font-weight: bold;
 padding-bottom: 4px;
}

.riv {
 font-size: 12px;
 color: #999;
}

.cart_img {
 max-width: 50px;
 max-height: 50px;
 position: absolute;
 right: 15px;
 bottom: 20px;
}

.only {
 width: auto;
 padding-top: 8px;
 display: inline-block;
 font-size: 12px;
 line-height: 16px;
 padding: 2px 8px 4px;
 color: rgb(95, 0, 128);
 font-weight: 500;
 background-color: rgb(244, 244, 244);
 margin-top: 10px;
}

ul li:first-child .only {
 display: none;
}